<template>
		<section id="exchange">
      <span class="title">{{$t('conversion.conversion')}}</span>
      <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
        <el-form :inline="true" :model="userForm" ref="userForm">
        	<!-- <el-form-item :label="$t('huobi')" prop="huobidui">
        		<el-select v-model="userForm.huobidui" :placeholder="$t('Pchoose')">
			      <el-option label="USDG" value="USDG"></el-option>
			      <el-option label="BTY" value="BTY"></el-option>
			    </el-select>
        	</el-form-item> -->

        	<el-form-item :label="$t('convert')" prop=" coins">
        		<el-select v-model="userForm.coins" :placeholder="$t('All')">
			      <el-option :label='$t("buy")' :value="1"></el-option>
			      <el-option :label='$t("sell")' :value="2"></el-option>
			    </el-select>
        	</el-form-item>
          <el-form-item :label="$t('CTFT')" prop="startTime">
              <el-date-picker type="date" :placeholder="$t('CD')" v-model="userForm.startTime"></el-date-picker>              
          </el-form-item>
          <el-form-item :label="$t('go')" prop="endTime">
              <el-date-picker type="date" :placeholder="$t('CD')" v-model="userForm.endTime"></el-date-picker>
          </el-form-item>
          <el-form-item prop="enterpriseName">
              <el-input :placeholder="$t('pleaseEnter')" v-model="input" class="input-with-select">
                <el-select v-model="select" slot="prepend" :placeholder="$t('Pchoose')">
                   <el-option label="UID" value="uid"></el-option>
                   <!-- <el-option :label="$t('phone')" value="phoneNumber"></el-option>
                   <el-option :label="$t('userName')" value="personName"></el-option>
                   <el-option :label="$t('email')" value="email"></el-option>  -->
                </el-select> 
              </el-input>
          </el-form-item>
          <el-form-item>
               <el-button class="warning" v-on:click="getUsers">{{$t('search')}}</el-button>
          </el-form-item>
          <el-form-item>
               <el-button class="info" @click="handleReset('userForm')">{{$t('reset')}}</el-button>                 
          </el-form-item>
        </el-form>
      </el-col>
      <el-table :data="userList"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" >
           <el-table-column prop="id"  :label="$t('conversion.tableHead[0]')">
            </el-table-column>
            <el-table-column prop="uid" :label="$t('conversion.tableHead[1]')">
            </el-table-column>
           <!--  <el-table-column prop=""  :label="$t('conversion.tableHead[2]')">
            </el-table-column> -->
            <el-table-column prop="tradeType"  :label="$t('conversion.tableHead[3]')">
            </el-table-column>
            <el-table-column prop="rate" :label="$t('conversion.tableHead[4]')">
            </el-table-column>
            <el-table-column prop="usdgAmount"  :label="$t('conversion.tableHead[5]')">
            </el-table-column>
            <el-table-column prop="ctime" :label="$t('conversion.tableHead[6]')">
            </el-table-column>
            <el-table-column prop="utime"  :label="$t('conversion.tableHead[7]')">
            </el-table-column>
      </el-table>
      <div class="page" v-show = "total >10">
        <el-col :span="24" class="toolbar" >
           <el-pagination background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange">
           </el-pagination>
        </el-col>
      </div>
   </section>
</template>
   
<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear,dateStrSecond} from '../../common/js/common.js'

export default {
   data(){
     return{
       input:"",
       select:"",
       startT:"",
       endT:"",
       userForm:{
       	 huobidui:"",
   		   coins:'',
   		   firstInstance:'',
   		   reviewOfficer:'',
         startTime:'',
         endTime:'',
         enterpriseName:''
       },
       userLoading:false,
       userList:[],
       total:0,
       exchangeData:{
          page:1,
          rowNum:'10',
       },
     }
   },
   methods:{
    queryExchange:function(){
      if(this.select != ""){
           this.userForm[this.select] = this.input;
         }
         if(this.userForm.startTime != ""){
          //  this.userForm.startTime = dateStr2GmtTimeStamp(this.userForm.startTime);
          let startTimeC = dateStr2GmtTimeStamp(this.userForm.startTime);
           // 获取秒的时间戳用于给后台传数据
           this.startT = dateStrSecond(startTimeC)
         }
         if(this.userForm.endTime != ""){
          //  this.userForm.endTime = dateStr2GmtTimeStamp(this.userForm.endTime);
          let endTimeC = dateStr2GmtTimeStamp(this.userForm.endTime);
           this.endT=dateStrSecond(endTimeC)
         }

         if(this.userForm.huobidui != ""){
            this.userForm.huobidui = this.userForm.huobidui;
         }
         if(this.userForm.coins != ""){
            this.userForm.coins = this.userForm.coins;
         }
          let data = {
          page:this.exchangeData.page,
          rowNum:this.exchangeData.rowNum,
          uid:"",
          personName:"",
          phoneNumber:"",
          email:"",
          startTime:this.startT,
          endTime:this.endT,
          tradeType:this.userForm.coins,
         };
          if(this.select=="uid"){
          data.uid = this.input;
         }else if(this.select=="phoneNumber"){
          data.phoneNumber = this.input;
         }else if(this.select == "personName"){
          data.personName = this.input;
        }else if(this.select=="email"){
          data.email = this.input;
        }
        console.log("bupi")
        console.log(data)
      ajax(this,this.ApiConfig.assetManagement.queryExchange,data,(res) => {
        if(res.code==200){
          console.log("返回是否有变化")
          console.log(res)
        this.userList = res.data.rows;
        let arr = this.userList;
        arr.forEach((item,i,arr) =>{
          arr[i].tradeType = arr[i].tradeType==1?"买":"卖";
          arr[i].ctime = getDataYear('Y-M-D h:m:s',arr[i].ctime);
          arr[i].utime = getDataYear('Y-M-D h:m:s',arr[i].utime);
        })
        this.total = res.data.total;
        this.startT = "";
        this.endT = "";
      }else{
        this.$notify({
          title:"提示",
          message:res.message,
          type:"warning"
        })
        return false;
      }
        
      },"POST")
      
    },
     getUsers:function(){
        this.queryExchange();
     },
     handleReset:function(forName){
        this.$refs[forName].resetFields();
        this.userForm.huobidui='';
        this.userForm.coins='';
        this.userForm.startTime='';
        this.userForm.endTime='';
        this.input="";
        this.select="";
        this.queryExchange();
     },
     tableRowClassName:function({row, rowIndex}){
         if(rowIndex % 2 == 1){
            return "success-row"
         }
     },
      handleCurrentChange:function(val){
       this.exchangeData.page = val;
       this.queryExchange();
     },


   },
   mounted:function(){
    this.queryExchange();
   }
}
</script>

<style>
#exchange{
  .el-select .el-input {
    width: 105px;
  }
  .el-col-24{
    min-width: 1209px;
  }
}
</style>





